import React from "react";
import Table from "react-bootstrap/Table";

const StaticTable = ({ columns, data, handleCheckboxChange, checkedItems }) => {
  console.log("StaticTable Columns prop:", columns); // Debugging line for columns
  console.log("StaticTable Data prop:", data); // Debugging line for data

  return (
    <Table bordered className="table-light">
      <thead>
        <tr>
          <th></th>
          <th>Column Name</th>
          <th>Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {data.map((row, index) => (
          <tr key={index}>
            <td>
              <input
                type="checkbox"
                checked={checkedItems[index] || false}
                onChange={() => handleCheckboxChange(index)}
              />
            </td>
            <td>{row.columnName}</td>
            <td>
              <select className="form-select">
                <option value={row.attributeName}>{row.attributeName}</option>
              </select>
            </td>
          </tr>
        ))}
      </tbody>
    </Table>
  );
};

export default StaticTable;
