import React, { useState } from "react"; // Corrected import for useState
import StaticTable from "../../components/bootsrap-table/Table";

const data = [
  { columnName: "Level", attributeName: "Level" },
  { columnName: "Title", attributeName: "Title" },
  { columnName: "Type", attributeName: "Type" },
  { columnName: "Name", attributeName: "Name" },
  { columnName: "Revision", attributeName: "Revision" },
  { columnName: "Description", attributeName: "Description" },
  {
    columnName: "Enterprise Item Number",
    attributeName: "Enterprise Item Number",
  },
  { columnName: "Title (Instance)", attributeName: "Title (Instance)" },
];

// Define columns for StaticTable based on DataTable's structure
const columns = ["", "Column Name", "Attribute Name"]; // Headers as in DataTable

const MappedList = () => {
  const [checkedItems, setCheckedItems] = useState({}); // Include useState as in DataTable example
  const handleCheckboxChange = (index) => {
    setCheckedItems({ ...checkedItems, [index]: !checkedItems[index] });
  };

  console.log("Data in MappedList before StaticTable:", data); // Debugging line

  return (
    <StaticTable
      columns={columns}
      data={data}
      handleCheckboxChange={handleCheckboxChange}
      checkedItems={checkedItems}
    />
  );
};

export default MappedList;
